<template>
  <div class="dialogue-info">
    <header id="wx-header">
      <!--<div class="other"><span>添加朋友</span></div>-->
      <div class="center">
        <div class="iconfont icon-return-arrow" v-on:click="$router.back()">
          <span>返回</span>
        </div>
        <span>更改名字</span>
        <div style="float:right;margin-right:5px;">
          <van-button type="primary" size="small" @click="confirm">完成</van-button>
        </div>
      </div>
    </header>
    <!-- <div style="padding:20px 0 10px 10px;color:#737475">用户名字</div> -->
    <van-cell-group>
      <div style="padding:0 15px;">
        <van-field v-model="value" placeholder="请输入名字" class="bottomColor" />
      </div>
    </van-cell-group>
    <div class="text">好的名字可以让你的朋友更容易记住你</div>
  </div>
</template>
<script>
import qs from "qs";
export default {
  data() {
    return {
      value: this.$route.query.nickname,
      userInfo: ""
    };
  },
  mounted() {
    this.userInfo = JSON.parse(window.localStorage.getItem("userInfo"));
  },
  methods: {
    confirm() {
      this.$http
        .post("/api/set/user/editNickName?nickName=" + this.value)
        .then(res => {
          console.log(res.data);
          if (res.data.code === 200) {
            this.$router.back();
          }
        });
    }
  }
};
</script>
<style>
.dialogue-info {
  background: white;
}
.bottomColor{
  border-bottom:1px solid rgb(89, 221, 89);
}
.text{
  font-size:14px;
  padding: 15px;
  color: rgb(151, 150, 150);
  letter-spacing:2px;
}
</style>


